.layout .expand-main {
  /* width: 80% !important; */
  transition: margin-left 0.9s ease-out;
  width: 100%;
  margin-left: 2%;
  margin-top: 1px;
  position: absolute;
  /* position: relative;
  top: 2%; */
}

.expand-main .homepage .background img{
  width: 400px;
}

.base {
  margin-top: 10%;
}

.homepage {
  margin-top: 20%;
}


.expand-main .homepage .effect .background{
  width:80% ;

}
.expand-main .homepage .effect img{
  width:80% ;
  
}

.expand-main .divider{

}
.homepage .effect {
  width: 40%;
  /* transition: width .9s ease-in-out; */
}
.homepage .data {
  width: 60%;
}
.homepage .effect:hover {
  /* width: 100%; */
  transform: translate3d(-4.50195px, -6.92986px, 0px);
  transform-style: preserve-3d;
  backface-visibility: hidden;
}

.homepage .background {
  /* position: absolute; */
  /* top: 15%; */
  /* padding: 3%; */
  width: 300px;
  height: 200px;
  background-color:var(--main-color)
  /*  
  z-index: 0; */
}

.homepage img {
  position: relative;
  bottom: 49%;
  left: 7%;
  border: 10px solid #0e0d0d;
  border-radius: 4px;
  padding: 20px;
  width: 250px;
}

.homepage p {
  margin-left: 2%;
