}
.upArrow {
  background-color: var(--main-color);
  width: 48px;
  /* margin-left: 90%; */
  /* height: 80px; */
  color: rgb(252, 251, 251);
  text-align: center;
}

.biography .container ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
.biography .container li {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}
.biography .container li p{
  border: 4px solid #444;
    background-color: #f2f2f2;
    padding: 7px 26px;
    line-height: 1.6;
    margin: 0 0 30px 30px;
    font-size: 17px;
    text-align: center;
}
.biography .cv .download {

 width: 280px;
 height: 190px;

}


.biography .cv .download span{

/* 
  width: 100%;
  height: 100%; */
}

.biography .cv .download i{
 /* position: absolute; */
  font-size: 50px;
  margin-top: 30%;
}


.decorLine{
 
}

.decorLine .small{
    height: 60px;
    border-left: 2px solid black;
}

.decorLine .long{
  height: 100px;
  border-left: 2px solid black;
}
 .biography .hovered .small{
    transform: translateY(10%);
}
.biography .hovered .long{
  transform: translateY(-3%);
}

.biography .exp .exp-btn{
  width: 150px;
  height: 40px;
  /* background: linear-gradient(to right,var(--main-color), #dff4f5) !important; */
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  /* background-color: var(--main-color)!important; */
  border: none;
  border-bottom: 2px solid var(--main-color);
  display: inline-block;
  margin-right: 5px;
}
.progress{
  border: 4px solid black;
  padding: 3px ;
}
.progress-bar{
  background-color: var(--main-color)!important; 
  color: black;
  font-family:  "Poppins", sans-serif;
  font-size: large;
}

.GitHubProfile h2{
  text-decoration: underline;
  text-decoration-color: var(--main-color);
}

.GitHubProfile img{
  border: 3px solid black;
  padding: 10px;
}
.GitHubProfile img:hover{
  border: 2px solid var(--main-color);
  transition: all .5s ease-in-out;
  width: 120px;
  height: 140px;
}
.GitHubProfile h3{
  color: var(--main-color);
}

.projects img{
  width: 300px;
  height: 300px;
}